@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500&display=swap');

html,
body {
  padding: 0;
  margin: 0;
  height: fit-content;
}

a {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
}

p {
  margin: 0;
  font-weight: 400;
}

* {
  box-sizing: border-box;
  font-family: 'Baloo 2', sans-serif !important;
}

.wentilabs_wave {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100px;
  margin: 0 auto;
  margin-top: 30px;
}

.wentilabs_wave_dot_1 {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  background: #004ebc;
  animation: wave 1.3s linear infinite;
}

.wentilabs_wave_dot_2 {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  background: #004ebc;
  animation: wave 1.3s linear infinite;
  animation-delay: -1.1s;
}
.wentilabs_wave_dot_3 {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  background: #004ebc;
  animation: wave 1.3s linear infinite;
  animation-delay: -0.9s;
}

@keyframes wave {
  0%,
  60%,
  100% {
    transform: initial;
  }
  30% {
    transform: translateY(-15px);
  }
}

.wentilabs_chat {
  z-index: 3;
  border-radius: 8px;
  width: 350px;
  max-height: 500px;
  height: 500px;
  cursor: auto;
  opacity: 1;
}

.wentilabs_chat_header {
  display: flex;
  align-items: center;
  height: 53px;
  background: #004ebc;
  padding: 12px 16px;
  border-top-left-radius: 12px;
  border-top-right-radius: 16px;
}

.wentilabs_chat_content {
  padding: 10px;
  margin: 0;
  overflow-y: auto;
  overflow-x: hidden;
  flex-grow: 1;
  background: transparent;
}

.wentilabs_chat_message_wrapper {
  width: 100%;
  align-items: end !important;
  justify-content: end;
  margin-bottom: 20px;
  padding-right: 5px;
}

.wentilabs_chat_message_wrapper_bot {
  width: 100%;
  align-items: start !important;
  margin-bottom: 20px;
  padding-right: 5px;
}

.wentilabs_chat_message_wrapper_chat_message_me {
  min-width: 80px;
  background-color: #004bec;
  color: white;
  padding: 12px 16px;
  border-radius: 20px 20px 5px 20px;
  font-size: 16px;
  line-height: 19px;
  max-width: 250px;
}

.wentilabs_chat_message_wrapper_bot_chat_message_bot {
  min-width: 80px;
  background-color: #f0f0f0;
  color: #1c1c1c;
  padding: 12px 16px;
  border-radius: 20px 20px 20px 5px;
  font-size: 16px;
  line-height: 19px;
  max-width: 250px;
}

.wentilabs_chat_message_wrapper_bot_chat_message_link_wrapper {
  background-color: #f0f0f0;
  padding: 12px;
  padding-bottom: 8px;
  border-radius: 15px;
  max-width: 250px;
}

.wentilabs_chat_message_wrapper_bot_chat_message_link_wrapper_chat_message_product_link {
  width: 100%;
  text-align: center;
  display: block;
  margin-top: 8px;
  color: #1c1c1c;
  font-size: 16px;
  line-height: 19px;
}

.wentilabs_chat_footer {
  width: 100%;
  margin-top: 10px;
  color: black;
  padding: 10px;
  padding-bottom: 6px;
}

.wentilabs_chat_footer_message_template_wrapper {
  width: 100%;
  justify-content: end;
  margin-bottom: 16px;
  cursor: pointer;
  gap: 10px;
  overflow-x: auto;
}

.wentilabs_chat_footer_message_template_wrapper_message_template_item {
  background-color: #004ebc;
  color: white;
  padding: 8px;
  border-radius: 15px;
  font-size: 14px;
}

.wentilabs_chat_footer_chat_input_group {
  width: 100%;
  background-color: #fafafa;
  height: 48px;
  border-radius: 45px;
  box-shadow: 0px 2px 4px 0px #0000001a;
  border: 1px solid #e6e6e6;
}

.wentilabs_chat_footer_chat_input_group_chat_input {
  color: #1c1c1c;
  background: transparent;
  height: 48px;
  border-radius: 30px;
  border: none;
  margin-right: 16px;
  margin-left: 4px;
  padding-left: 16px;
  width: 100%;
  font-size: 1rem;
}

.wentilabs_chat_footer_chat_input_group_chat_input:focus-visible {
  outline: none;
}

.wentilabs_chat_footer_chat_input_group_chat_input::placeholder {
  color: #1c1c1c;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  opacity: 30%;
}

.wentilabs_chat_footer_chat_input_group_chat_input_right_element {
  height: 48px;
  margin-right: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wentilabs_chat_footer_chat_powered_by {
  width: 100%;
  text-align: center;
  font-size: 9px;
  margin-top: 5px;
  line-height: 9px;
}

.wentilabs_chat_footer_chat_disclamer {
  width: 100%;
  text-align: center;
  font-size: 10px;
  margin-top: 5px;
  line-height: 10px;
}

.wentilabs_chat_footer_chat_powered_by_link {
  text-decoration: underline;
  color: #5e50a0;
  font-size: 10px;
}

.wentilabs_chat_button {
  outline: 0;
  z-index: 3;
  position: fixed;
  right: 25px;
  bottom: 90px;
  display: block;
  background: #004ebc;
  width: 47px;
  height: 47px;
  border: 0;
  border-radius: 100%;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 32px rgba(0, 0, 0, 0.16);
  transition: box-shadow 13200ms ease;
}
.wentilabs_chat_button:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09), 0 4px 40px rgba(0, 0, 0, 0.24);
}

.wentilabs_chat_button_chat_icon__close {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: top left;
  width: 14px;
  height: 14px;
}

.wentilabs_chat_button_chat_icon__launcher {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: top left;
  width: 32px;
  height: 36px;
}

.wentilabs_chat_base {
  position: fixed;
  z-index: 3;
  right: 25px;
  bottom: 145px;
  background: #fff;
  width: 360px;
  height: 100%;
  max-height: 650px;
  border-radius: 8px;
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.16);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.wentilabs_chat_wentilabs_chat_header_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 53px;
  background: #004ebc;
  padding: 12px 16px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
}

.wentilabs_chat_wentilabs_chat_header_text {
  font-size: 18px;
  line-height: 28.84px;
  color: white;
}

.wentilabs_chat_wentilabs_chat_header_icon {
  width: 17px;
  height: 17px;
  cursor: pointer;
}
